  padding: 3% 10%;
}

.read-aloud-header {
  font-family: "Lobster", cursive;
  font-size: 2rem;
  letter-spacing: 4px;
  margin-top: 6%;
  margin-bottom: 5%;
  color: black;
  border-bottom: 2px solid;
}

.inner-container-img {
  width: 40%;
}

.inner-container-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.inner-container-books-events {
  padding-top: 70px;
  position: relative;
  margin-bottom: 3%;
  display: flex;
  justify-content: space-around;
}

.inner-container-books,
.inner-container-events {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-bottom: 3%;
  width: 45%;
}

.inner-container-books-events:before {
  content: " ";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.1;
  background-image: url(/read-aloud/static/media/background.3948aa86.jpg);
  background-size: cover;
  z-index: -10;
}

.inner-container-books-events .label {
  background-color: rgb(198, 214, 202);
  color: #fff;
  font-size: 1.2em;
  font-weight: bold;
  margin: 14px 40px;
  padding: 8px 18px 8px 30px;
  position: relative;
  border-radius: 30px;
  width: 212px;
}

.inner-container-books-events .label:after {
  background-color: #fff;
  border-radius: 50%;
  content: "";
  height: 10px;
  position: absolute;
  top: 16px;
  left: 9px;
  width: 10px;
}

.inner-container-books-events .label:hover,
.inner-container-books-events .label.selected {
  box-shadow: 4px 5px 4px 1px rgb(0 0 0 / 50%);
  background-color: rgb(133, 159, 160);
  cursor: pointer;
}

.label-container {
  text-align: left;
  padding-left: 5%;
}

.books-container-items,
.events-container-items {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

@media screen and (max-width: 700px) {
  .inner-container-books-events {
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
  }

  .label-container {
    padding-left: 0;
    align-self: center;
}
.events-container,
.books-container{

}
