.popup-author,
.popup-category,
.popup-postedBy,
.popup-title {
  position: relative;
  display: inline-block;
  cursor: pointer;
  -webkit-appearance: none;
          appearance: none;
  margin: 0px;
}

.popuptext-author,
.popuptext-category,
.popuptext-postedBy,
.popuptext-title {
  visibility: hidden;
  width: 160px;
  background-color: #555;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 8px 0;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  margin-left: -80px;
}

.popuptext-author::after,
.popuptext-title::after,
.popuptext-category::after,
.popuptext-postedBy::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #555 transparent transparent transparent;
}

.popup-category:hover ~ .popuptext-category,
.popup-postedBy:hover ~ .popuptext-postedBy,
.popup-author:hover ~ .popuptext-author,
.popup-title:hover ~ .popuptext-title {
  visibility: visible;
  animation: fadeIn 1s;
}

input.popup-title:checked ~ i.fa-bookmark,
input.popup-postedBy:checked ~ i.fa-id-badge,
input.popup-category:checked ~ i.fa-list-ul,
input.popup-author:checked ~ i.fa-edit {
  color: red;
}

