.spinner-container {
    padding: 10% 8%;
    background-color: white;
    display: flex;
    width: 100%;
  }
  
  /* generated by https://loading.io/ */
  
  @keyframes spinner-element {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
  .spinner-element div {
    left: 108.80999999999999px;
    top: 56.16px;
    position: absolute;
    animation: spinner-element linear 1.7857142857142856s infinite;
    background: #292664;
    width: 16.38px;
    height: 28.08px;
    border-radius: 8.19px / 14.04px;
    transform-origin: 8.19px 60.839999999999996px;
  }
  .spinner-element div:nth-child(1) {
    transform: rotate(0deg);
    animation-delay: -1.6369047619047616s;
    background: #292664;
  }
  .spinner-element div:nth-child(2) {
    transform: rotate(30deg);
    animation-delay: -1.488095238095238s;
    background: #292664;
  }
  .spinner-element div:nth-child(3) {
    transform: rotate(60deg);
    animation-delay: -1.3392857142857142s;
    background: #292664;
  }
  .spinner-element div:nth-child(4) {
    transform: rotate(90deg);
    animation-delay: -1.1904761904761905s;
    background: #292664;
  }
  .spinner-element div:nth-child(5) {
    transform: rotate(120deg);
    animation-delay: -1.0416666666666665s;
    background: #292664;
  }
  .spinner-element div:nth-child(6) {
    transform: rotate(150deg);
    animation-delay: -0.8928571428571428s;
    background: #292664;
  }
  .spinner-element div:nth-child(7) {
    transform: rotate(180deg);
    animation-delay: -0.744047619047619s;
    background: #292664;
  }
  .spinner-element div:nth-child(8) {
    transform: rotate(210deg);
    animation-delay: -0.5952380952380952s;
    background: #292664;
  }
  .spinner-element div:nth-child(9) {
    transform: rotate(240deg);
    animation-delay: -0.4464285714285714s;
    background: #292664;
  }
  .spinner-element div:nth-child(10) {
    transform: rotate(270deg);
    animation-delay: -0.2976190476190476s;
    background: #292664;
  }
  .spinner-element div:nth-child(11) {
    transform: rotate(300deg);
    animation-delay: -0.1488095238095238s;
    background: #292664;
  }
  .spinner-element div:nth-child(12) {
    transform: rotate(330deg);
    animation-delay: 0s;
    background: #292664;
  }
  .spinner {
    margin: auto;
    width: 234px;
    height: 234px;
    display: inline-block;
    overflow: hidden;
  }
  .spinner-element {
    width: 100%;
    height: 100%;
    position: relative;
    transform: translateZ(0) scale(1);
    backface-visibility: hidden;
    transform-origin: 0 0;
  }
  .spinner-element div {
    box-sizing: content-box;
  }
  @media screen and (max-width: 480px) {
    .spinner-container {
      padding: 50px 0px ;
    }
  }